div.dim {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
}

div.overlay {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 550px;
  height: 350px;
  border-radius: 15px;
  background-color: #C6A79E;
  box-shadow: 0 5px 20px rgba(0,0,0,0.3);
  display: flex;
  flex-direction: column;
  z-index: 10;
}

div.share-overlay {
  width: 400px;
  height: 500px;
}

div.close {
  display: flex;
  flex-direction: row;
  justify-content: right;
  padding-right: 15px;
  padding-top: 10px;
}

img.x:hover {
  cursor: pointer;
}

img.x {
  height: 15px;
}

div.form {
  display: flex;
  flex-direction: row;
  padding: 25px 50px;
  position: relative;
}

div.create-session {
  display: flex;
  flex-direction: row;
  justify-content: right;
  padding: 20px 50px;
}

button.create-session {
  border: 0;
  height: 30px;
  width: 150px;
  border-radius: 5px;
  background-color: #DECFCE;
}

button.create-session:hover {
  cursor: pointer;
}

p.session-type {
  font-size: 20px;
  margin: 0;
  transform: translateY(3px);
}

p.fill {
  position: absolute;
  font-size: 20px;
  transform: translateY(-25px);
  transition: 0.6s ease-in-out;
  color: rgb(85, 85, 85);
  font-weight: 300;
}

p.filled {
  transform: translateY(-30px);
  font-size: 12px;
}

input.input {
  font-size: 20px;
  border: 0;
  border-bottom: 2px solid rgba(85, 85, 85, 0.5);
  background-color: rgba(0,0,0,0);
  z-index: 3;
}

input.input:focus {
  outline: none;
}

div.form:focus-within p.fill {
  transform: translateY(-30px);
  font-size: 12px;
}

div.selection {
  padding: 0 30px;
  display: flex;
  flex-direction: column;
}

div.module-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-top: 20px;
  row-gap: 10px;
}

p.modules {
  font-weight: bold;
  font-size: 18px;
  margin: 0;
}

p.no-mods {
  margin: 0;
  font-style: italic;
  color: grey;
  padding-top: 50px;
  align-self: center;
}

div.create-room-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: right;
}

button.create-room {
  height: 40px;
  width: 120px;
  margin: 20px;
  background-color: #DECFCE;
  border: 0;
  border-radius: 8px;
}

button.create-room:hover {
  cursor: pointer;
}

button.greyed-out {
   background-color: rgb(186,186,186);
}

button.greyed-out:hover {
  cursor: not-allowed;
}

.switch {
  font-size: 17px;
  position: relative;
  display: inline-block;
  width: 3.5em;
  height: 2em;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  inset: 0;
  border: 1.5px solid rgb(147, 147, 147);
  border-radius: 50px;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.slider:before {
  position: absolute;
  content: "1";
  height: 1.4em;
  width: 1.4em;
  left: 0.2em;
  bottom: 0.2em;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:#DECFCE;
  border-radius: inherit;
  transition: all 0.4s cubic-bezier(0.23, 1, 0.320, 1);
}

.switch input:checked + .slider:before {
  transform: translateX(1.5em);
  content:'2';
}

div.switch-container {
  display: flex;
  flex-direction: row;
  padding: 25px;
}

div.flex-jb {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

p.sem {
  margin: 0;
  font-size: 18px;
  padding-right: 5px;
  transform: translateY(3px);
}